import React, {useState, useEffect} from 'react'
import './App.css'
import api from './services/api'

function App() {
  const [teste, setTeste] = useState()

  useEffect(() =>{
    async function loadScripts(){
      const res = await api.get('/');
      
      setTeste(res.data);
    }

    loadScripts();
  }, [])

  return (
    <h1>Hello World {JSON.stringify(teste)}</h1>
  );
}

export default App;
